let _this, page = 1,
	loginRes, isEnd = false;
export default {
	data() {
		return {
			myFace: '',
			userInfo: {
				artCount: 0,
				u_remainder: 0,
				u_integral: 0
			},
			arts: [],
			host: ''
		}
	},
	onLoad() {
		_this = this;
		_this.host = _this.staticServer;
		loginRes = _this.checkLogin('../me/me', 2);
		if (!loginRes) {
			return false;
		}
		_this.myFace = loginRes[3];
	},
	onShow() {
		uni.request({
			url: _this.apiServer + "my&m=info",
			data: {
				uid: uni.getStorageSync("uid"),
				random: uni.getStorageSync("urandom")
			},
			method: "POST",
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success(res) {
				if (res.data.status == 'ok') {
					let user = res.data.data;
					_this.userInfo = {
						u_integral: user.u_integral,
						u_remainder: user.u_remainder,
						artCount: user.artCount
					}
				} else {
					uni.showToast({
						title: res.data.data,
						icon: "none"
					});
				}
			}
		});
		_this.init();
		_this.getArtList();
	},
	methods: {
		init() {
			page = 1;
			isEnd = false;
			_this.arts = [];
		},
		getArtList() {
			uni.showLoading({
				title: "正在加载...",
				mask: !0
			})
			uni.request({
				url: _this.apiServer + "my&m=arts&page=" + page,
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					uid: uni.getStorageSync("uid"),
					random: uni.getStorageSync("urandom")
				},
				success(res) {
					let status = res.data.status;
					if (status == 'empty') {
						uni.showToast({
							title: "文章已全部加载"
						})
					} else if (status == 'ok') {
						_this.arts = _this.arts.concat(res.data.data);
						page++;
					}
				},
				complete() {
					uni.hideLoading();
				}
			})
		},
		operateAction(e) {
			console.log(e);
			let art_id = e.currentTarget.id, index = e.currentTarget.dataset.index;
			uni.showActionSheet({
				itemList: ['编辑文章', '查看文章', '删除文章'],
				success(res) {
					if(res.tapIndex == 0) {
						uni.navigateTo({
							url: "../artedit/artedit?art_id="+art_id
						})
					}else if(res.tapIndex == 1) {
						uni.navigateTo({
							url: "../info/info?art_id="+art_id
						})
					}else{
						_this.removeArt(art_id, index);
					}
				}
			})
		},
		removeArt(art_id, index) {
			_this.showModal("你确定要删除文章吗？", function(res){
				if(res.confirm) {
					console.log(art_id);
					uni.request({
						url: _this.apiServer + "my&m=removeArt",
						method: "POST",
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						data: {
							uid: uni.getStorageSync("uid"),
							random: uni.getStorageSync("urandom"),
							artId: art_id
						},
						success(res) {
							if(res.data.status == 'ok') {
								uni.showToast({title: "已删除", icon:"none"});
								_this.arts.splice(index, 1);
							}else{
								uni.showToast({
									title: res.data.data,
									icon: "none"
								})
							}
						}
					})
				}
			})
		}
	}
}
